<style scoped>
.classify {
  box-shadow: 0 8px 16px 0 rgba(28, 31, 33, 0.2);
}
.classify_header,
.ganclassify_header {
  width: 1386px;
  margin: 0 auto 24px;
}
.classify_header > a {
  letter-spacing: 1px;
  line-height: 72px;
  color: black;
  font-size: 16px;
  font-weight: bold;
  margin-right: 40px;
  position: relative;
}
.classify_header > a::after {
  position: absolute;
  bottom: -5px;
  left: 50%;
  transform: translateX(-50%);
  content: "";
  display: none;
  width: 16px;
  height: 3px;
  background: #c80;
}
.classify_header > a:hover {
  color: #c80;
}
.classify_header > a:hover::after {
  display: inline-block;
}
.ganclassify_header {
  display: flex;
  flex-wrap: wrap;
}
.ganclassify_header > a {
  color: black;
  margin-right: 18px;
  width: auto;
  padding: 8px 4px;
}
.classify_header > .router-link-active {
  color: #c80;
}
.classify_header > .router-link-active::after {
  display: inline-block;
}
.ganclassify .router-link-exact-active {
  color: #c80;
  background: rgba(204, 136, 0, 0.1);
  border-radius: 15%;
}
</style>
<template>
  <div>
    <div class="classify">
      <div class="classify_header">
        <router-link to="/pay/0">全部</router-link>
        <router-link
          :to="/pay/ + ltem.id"
          v-for="(ltem, index) in list"
          :key="index"
        >{{ ltem.name }}</router-link>
      </div>
    </div>

    <div class="ganclassify">
      <div class="ganclassify_header">
        <router-link :to="/pay/ + id">全部</router-link>
        <router-link
          :to="/pay/ + ltem.fid + '/' + ltem.id"
          v-for="(ltem, index) in ganclassify"
          :key="index"
        >{{ ltem.name }}</router-link>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      list: [],
      ganclassify: [],
      id: 1
    };
  },
  methods: {
    classifycontent() {
      this.id = this.$router.history.current.params.id;
      if (this.$router.history.current.params.id > 0) {
        this.$axios({
          method: "POST",
          url:
            "http://127.0.0.1:3000/api/list/" +
            this.$router.history.current.params.id
        }).then(res => {
          this.ganclassify = res.data.data;
        });
      } else {
        this.$axios({
          url: "http://127.0.0.1:3000/api/ganclassify"
        }).then(res => {
          this.ganclassify = res.data.data; //全部子分类
        });
      }
    },
    allgenclassify() {
      this.$axios({
        url: "http://127.0.0.1:3000/api/",
        headers: {
          authorization: "Bearer " + window.localStorage.getItem("token")
        }
      }).then(res => {
        this.list = res.data.data; //全部分类
      });
    }
  },
  mounted() {
    this.allgenclassify();
    this.classifycontent();
  },
  watch: {
    "$route.path"() {
      this.classifycontent();
    }
  }
};
</script>
